<template>
    <div class="m-product-detail" v-if="product_info">
      <div class="m-product-swipe">
        <mt-swipe :auto="2000" >
          <mt-swipe-item v-for="item in product_info.images" v-bind:key="item.piid">
            <img :src="item.pipic" class="m-swipe-img" alt="">
          </mt-swipe-item>
        </mt-swipe>
        <span class="m-icon-back" @click="changeBack"></span>
      </div>
      <div class="m-product-detail-info">
        <h3>
          <span class="m-product-title">{{product_info.prtitle}}</span>
          <span class="m-red">￥1668.0</span>
        </h3>
        <div class="m-info-list">
          <span>快递：{{product_info.prfreight}}</span>
          <span>月销：{{product_info.month_sale_value}}</span>
          <span>
            {{product_info.brand.pbname}}
          </span>
        </div>
      </div>
      <div class="m-product-detail-more" @click="changeModal('show_sku',true)">
        <div>
          <span class="m-label">规格</span>
          <span class="m-select m-black">选择

            <template  v-if="select_value">
               <span v-for="(item,index) in select_value.skuattritedetail">{{product_info.prattribute[index]}} {{item}}</span>
            </template>
             <template  v-else>
                 <span v-for="item in product_info.prattribute">{{item}} </span>
             </template>
          </span>
        </div>
        <div>
          <span class="m-more"></span>
        </div>
      </div>
      <div class="m-product-detail-more" @click="changeRoute('/evaluate')">
        <div>
          <span class="m-label">评价</span>
          <span class="m-start active"></span>
          <span class="m-start active"></span>
          <span class="m-start active"></span>
          <span class="m-start active"></span>
          <span class="m-start "></span>
        </div>
        <div>
          <span class="m-ft-20">查看详情</span>
          <span class="m-more"></span>
        </div>
      </div>
      <div class="m-product-detail-img-box">
        <img :src="product_info.prdesc"  alt="">
      </div>
      <div class="m-product-detail-foot">
        <span class="m-icon-car" @click.stop="changeRoute('/shop')"></span>
        <span class="m-icon-service"></span>
        <div class="m-product-detail-btn">
          <span @click="addCart">加入购物车</span>
          <span class="active" @click="buyNow">立即购物车</span>
        </div>
      </div>

      <sku v-if="show_sku" :now_select="select_value" :now_num="canums" :product="product_info" @changeModal="changeModal" @sureClick="sureClick"></sku>
    </div>
</template>

<script>
  import sku from '../components/sku';
  import common from '../../../common/js/common';
  import axios from 'axios';
  import api from '../../../api/api';
  import {Toast} from 'mint-ui'
  var scroll = (function (className) {
    var scrollTop;
    return {
      afterOpen: function () {
        scrollTop = document.scrollingElement.scrollTop || document.body.scrollTop;
        document.body.classList.add(className);
        document.body.style.top = -scrollTop + 'px';
      },
      beforeClose: function () {
        document.body.classList.remove(className);
        document.scrollingElement.scrollTop = scrollTop;
        document.body.scrollTop = scrollTop;
      }
    };
  })('scroll');
    export default {
        data(){
          return{
            show_sku:false,
            product_info:null,
            sku:null,
            select_value:null,
            canums:1
          }
        },
      components:{
          sku
      },
      mounted(){
        common.changeTitle('商品详情');
        this.getInfo();
      },
      methods:{
         // 改变模态框
         changeModal(v,bool) {
           this[v] = bool;
           if(bool){
             scroll.afterOpen();
           }else{
             scroll.beforeClose();
           }
         },
        //改变路由
        changeRoute(v){
           this.$router.push(v);
        },
        //返回{
        changeBack(){
          this.$router.go(-1);
        },
        //获取商品信息
        getInfo(){
           axios.get(api.product_get,{
             params:{
               prid:this.$route.query.prid
             }
           }).then(res => {
             if(res.data.status == 200){
               this.product_info = res.data.data;
             }
              else{
               Toast({ message: res.data.message,duration:1000, className: 'm-toast-fail' });
             }
           },error => {
             Toast({ message: error.data.message,duration:1000, className: 'm-toast-fail' });
           })
        },
        //购物车确定
        sureClick(item,num){
          this.canums = num;
          this.select_value = item;
          this.postCart();
          this.changeModal('show_sku',false);
        },
      //  加入购物请求
        postCart(){
          axios.post(api.cart_create + '?token=' + localStorage.getItem('token'),{
            skuid:this.select_value.skuid,
            canums:this.canums
          }).
          then(res => {
            if(res.data.status == 200){
              Toast({ message: res.data.message,duration:1000, className: 'm-toast-success' });
            }else{
              Toast({ message: res.data.message,duration:1000, className: 'm-toast-fail' });
            }
          },error => {
            Toast({ message: error.data.message,duration:1000, className: 'm-toast-fail' });
          })
        },
      //  加入购物车
        addCart(){
           if(this.select_value){
               this.postCart();
           }else{
             this.show_sku = true;
           }
        },
        //立即购买
        buyNow(){
          if(this.select_value){

          }else{
            this.show_sku = true;
          }
        }
      }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
  @import "../../../common/css/index";
.m-product-detail{
  padding-bottom: 200px;
  .m-product-swipe{
    position: relative;
    width: 100%;
    height: 784px;
    .m-icon-back{
      position: absolute;
      top: 20px;
      left: 68px;
      width: 24px;
      height: 41px;
      background: url("/static/images/icon-detail-back.png") no-repeat;
      background-size: 100% 100%;
    }
  }
  .m-product-detail-info{
    padding: 30px;
    box-shadow:0px 3px 6px rgba(0,0,0,0.16);
    h3{
      display: flex;
      flex-flow: row;
      align-items: flex-start;
      justify-content: space-between;
      font-size: 30px;
      font-weight: bold;
      color: #666666;
      margin-bottom: 40px;
      .m-product-title{
        /*width: 625px;*/
        /*overflow: hidden;*/
        /*text-overflow: ellipsis;*/
        /*white-space: nowrap;*/
        text-align: left;
      }
      .m-red{
        color: #EF9B2D;
      }
    }
    .m-info-list{
      display: flex;
      flex-flow: row;
      align-items: center;
      justify-content: space-between;
      color: #999999;
    }
  }
  .m-product-detail-more{
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: space-between;
    padding: 24px 30px;
    color: #999999;
    box-shadow:0px 3px 6px rgba(0,0,0,0.16);
    .m-label{
      display: inline-block;
      margin-right: 30px;
    }
    .m-black{
      color: #666666;
    }
    .m-more{
      display: inline-block;
      width: 13px;
      height: 19px;
      background: url("/static/images/icon-right.png") no-repeat;
      background-size: 100% 100%;
      margin-left: 20px;
    }
  }
  .m-product-detail-img-box{
    margin: 10px 0;
    img{
      display: block;
      width: 100%;
      /*height: 600px;*/
      /*background-color: #9fd0bf;*/
    }
  }
  .m-product-detail-foot{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 26px 0 98px;
    background-color: #fff;
    text-align: left;
    .m-icon-car{
      display: inline-block;
      width: 53px;
      height: 53px;
      background: url("/static/images/icon-car.png") no-repeat;
      background-size: 100% 100%;
      margin: 0 29px 0 15px;
      vertical-align: middle;
    }
    .m-icon-service{
      display: inline-block;
      width: 53px;
      height: 53px;
      background: url("/static/images/icon-service.png") no-repeat;
      background-size: 100% 100%;
      margin-right: 20px;
      vertical-align: middle;
    }
    .m-product-detail-btn{
      display: inline-block;
      height: 62px;
      line-height: 62px;
      span{
        display: inline-block;
        width: 271px;
        text-align: center;
        background-color: #fbe891;
        margin: 0;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        &.active{
          background-color: #fcd316;
          margin-left: -8px;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          border-top-right-radius: 10px;
          border-bottom-right-radius: 10px;
        }
      }
    }
  }

}

</style>



